import axios from 'axios'
import qs from 'qs';
import {getLimited,setLimited,getLimitedUrl,setLimitedUrl,getToken} from './auth';
import {Loading, Message} from 'element-ui'
/*import store from '../store'*/

var  loadinginstace;
let baseURL='https://www.embracex.com';
let URL='https://www.embracex.com/gcsweixin/shop';

let config={
  loginUrl:`${URL}/login`,  /**/
  loginApi:`${baseURL}/gcsweixin/shop/login`,    /*API*/
  logoutApi:`${baseURL}/logout`,  /*API*/
  indexUrl:`${URL}/home` /**/
};
const service = axios.create({
  baseURL, // apibase_url
  //timeout: 15000,                  // */
  withCredentials: true
});

// request

service.interceptors.request.use(config => {
  //console.log(config);

  if (config.headers['Content-Type'] === "application/x-www-form-urlencoded") {
    config.data = qs.stringify(config.data);

  }

  if (getToken()) {
    config.headers['x-auth-token'] = getToken() // token 
  }

  loadinginstace = Loading.service({fullscreen: true})
  return config
}, error => {
  loadinginstace.close()
  Message.error({
    message: ''
  })
  return  Promise.reject(error)
});

// respone
service.interceptors.response.use(
  response => {

    loadinginstace.close()

    let request = response.request;
    // console.log(request.responseURL);
    // console.log(config.loginApi)
    if (request.responseURL === config.loginApi && request.status === 200 ) {  /!**!/
      if(getLimited()==='true'){
        setLimited('false');
        location.href=getLimitedUrl();

      }
      else{

        location.href=config.indexUrl;
      }
    }
    else if (request.responseURL === config.logoutApi && request.status === 200 ) {   /!**!/
      location.href=config.loginUrl;

    }
    return response;
  },
  error => {
    let errcode = String(error);
    if (errcode.indexOf('401') > 0) {
      if (location.href === config.loginUrl) {    /*401*/

        alert('');/**/


      }
      else {     /**/
        setLimited('true');
        setLimitedUrl(location.href);
        location.href = config.loginUrl;
      }
    }
    else if (errcode.indexOf('403') > 0) {      /*403*/
      alert('');/**/
    }
    loadinginstace.close();
    Message.error({
      message: ''
    })
    return Promise.reject(error)
  }
);
export default service



