<template>
  <el-row>
    <el-col :span="24">
      <el-form :model="goodsup" :rules="rules" ref="OupForm" label-width="150px"
               style="width: 65%;margin: 0 0 80px 0 ">
        <el-form-item label=" :" prop="name">
          <el-input v-model="goodsup.name"></el-input>
        </el-form-item>
        <el-form-item label=" :" prop="old_price">
          <el-input type="number" v-model.number="goodsup.old_price"></el-input>
        </el-form-item>
        <el-form-item label=" :" prop="price">
          <el-input type="number" v-model.number="goodsup.price"></el-input>
        </el-form-item>
        <el-form-item label=" :">
          <el-upload
            class="avatar-uploader"
            action="https://www.embracex.com/gcsweixin/shop/product/uploadimg/"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
            <img v-if="goodsup.logo" :src="goodsup.logo " class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label=" :" style="width: 200%">
          <el-upload
            action="https://www.embracex.com/gcsweixin/shop/product/uploadimg/"
            list-type="picture-card"
            :file-list="fileList"
            :limit="5"
            :on-exceed="handleExceed"
            :on-success="handlesuccess"
            :on-preview="handlePictureCardPreview"
            :on-remove="handleRemove"
            :before-upload="beforeAvatarUpload">
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </el-form-item>
        <el-form-item label=" :" style="width: 150%" prop="heading">
          <el-input v-model="goodsup.heading" placeholder="20"></el-input>
        </el-form-item>
        <el-form-item label=" :" prop="description" style="width: 150%;height: 150px">
          <el-input type="textarea" v-model="goodsup.description" :autosize="{ minRows: 5, maxRows: 8}"
                    placeholder="100"></el-input>
        </el-form-item>
        <el-form-item label=" :" style="width: 200%" prop="stock">
          <el-input v-model="goodsup.stock" style="width: 36%" placeholder=""></el-input>
          <label style="margin: 0 0 0 15px;color: red">9999999</label>
        </el-form-item>
        <el-form-item label=" :" style="width: 200%" prop="date">
          <el-date-picker
            v-model="goodsup.date"
            type="datetime"
            style="width: 36%"
            placeholder="">
          </el-date-picker>
          <label style="margin: 0 0 0 15px;color: red">o</label>
        </el-form-item>
        <el-form-item label="  :" style="width: 200%" prop="sort">
          <el-input v-model="goodsup.sort" style="width: 36%" placeholder=""></el-input>
          <label style="margin: 0 0 0 15px;color: red">o</label>
        </el-form-item>
        <el-form-item style="margin: 50px 0 0 0">
          <el-button style="margin: 0 50px 0 0;" type="primary" @click="submitForm()"></el-button>
          <el-button @click="resetForm('goodsup')"></el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>
<script>

  export default {
    data() {
      let oldprice = (rule, value, callback) => {
        if (/^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/.test(value) === false) {
          callback(new Error(''));
        } else {
          callback();
        }
      };
      let price = (rule, value, callback) => {
        if (/^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/.test(value) === false) {
          callback(new Error(''));
        } else if (value > this.goodsup.old_price) {
          callback(new Error(''));
        }
        else {
          callback();
        }
      };
      let stock = (rule, value, callback) => {
        if (/^[1-9]\d*$/.test(value) === false) {
          callback(new Error(''));
        } else if (value > 9999999) {
          callback(new Error('9999999'));
        }
        else {
          callback();
        }
      };
      let sort = (rule, value, callback) => {
        if (/^[1-9]\d*$/.test(value) === false) {
          callback(new Error(''));
        } else if (value > 9999999) {
          callback(new Error('9999'));
        }
        else {
          callback();
        }
      };


      return {
        imageUrl: '',
        dialogImageUrl:'',
        dialogVisible: false,
        imgers: [],
        imgay:[],
        fileList: [],
        tijiao: {},
        goodsup: {},
        tupian:[],
        rules: {
          name: [
            {required: true, message: '', trigger: 'blur'},
            {min: 1, max: 15, message: ' 1  15 ', trigger: 'blur'}
          ],
          old_price: [
            {required: true, message: '',},
            {validator: oldprice, trigger: 'blur'}
          ],
          price: [
            {required: true, message: '',},
            {validator: price, trigger: 'blur'}
          ],
          heading: [
            {required: true, message: '', trigger: 'blur'},
            {max: 20, message: '20', trigger: 'blur'}
          ],
          date: [
            {type: 'date', required: true, message: '', trigger: 'change'}
          ],
          description: [
            {required: true, message: '', trigger: 'blur'},
            {max: 100, message: '100', trigger: 'blur'}
          ],
          stock: [
            {validator: stock, trigger: 'blur'}
          ],
          sort: [
            {validator: sort, trigger: 'blur'}
          ],
        },
      };
    },
    methods: {
      getgoods(){
        this.oupid = this.$route.params.id
        this.axios({
          method: 'post',
          url: '/gcsweixin/shop/product/getproduct',
          params: {
            id: this.oupid
          }
        }).then((res) => {
          console.log(res.data);
          this.goodsup = res.data;
          this.fileList = this.tpjq(this.goodsup.image);
          let img=this.goodsup.image;
          let imgjq = img.substring(0,img.length);
          let imgs = imgjq.split(',');
          this.imgay=imgs;
          console.log(this.imgay)
        })

      },
      submitForm() {
        if(this.imgay.remove('')){
          this.imgay.remove('');
        }

        if (this.goodsup.stock === '') {
          this.goodsup.stock = 9999999;
        }
        if (this.goodsup.sort === '') {
          this.goodsup.sort = '0';
        }
        if (this.goodsup.name != '' && this.goodsup.old_price != '' && this.goodsup.price != ''
          && this.goodsup.date != '' && this.goodsup.heading != '' && this.goodsup.description != '') {
          this.tijiao = {
            'name': this.goodsup.name,
            'old_price': this.goodsup.old_price,
            'price': this.goodsup.price,
            'logo': this.goodsup.logo,
            'image': this.imgay.toString(),
            'date': this.goodsup.date,
            'heading': this.goodsup.heading,
            'description': this.goodsup.description,
            'stock': this.goodsup.stock,
            'status': this.goodsup.status,
            'sort': this.goodsup.sort,
            'type': this.goodsup.type,
          };
          this.axios({
            method: 'post',
            url: '/gcsweixin/shop/product/update',
            params: {
              productid: this.oupid
            },
            data: this.tijiao
          }).then((res) => {
             this.$router.push({path: '/gservice'});
            this.$message({
              message: '',
              type: 'success'
            });
          })
        } else {
          this.$message.error('');

        }

      },

      resetForm(formName) {
         this.$router.push({path: '/gservice'});

      },

      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      handleAvatarSuccess(response) {
        this.goodsup.logo = response;
        console.log(this.goodsup.logo)
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error(' JPG !');
        }
        if (!isLt2M) {
          this.$message.error(' 2MB!');
        }
        return isJPG && isLt2M;
      },
      handleRemove(file, fileList) {
        Array.prototype.remove = function(val) {
          let index = this.indexOf(val);
          if (index > -1) {
            this.splice(index, 1);
          }
        };

        if(file.response===undefined && file.url!==''){
          this.imgay.remove(file.url);
        }
        else{
          this.imgay.remove(file.response);
        }


      },
      handlesuccess(file, fileList){

        this.imgay.push(file)

      },

      handleExceed(files, fileList) {
        this.$message.warning(` 5  ${files.length}  ${files.length + fileList.length} `);
      },
      tpjq(qaq) {
        let img = qaq;
        let imgjq = img.substring(0, img.length);
        let imgs = imgjq.split(',');
        let geshi = [];

        for (let i = 0; i < imgs.length; i++) {
          let qa = imgs[i];
          let qaa = qa.split('.');
          geshi.push(qaa[qaa.length - 1]);
        }

        let images = [];
        for (let i = 0; i < imgs.length; i++) {
          images.push({name: i + 1 + '.' + geshi[i], url: imgs[i]})
        }

        return images;
      },
    },
    mounted(){
      this.getgoods();
    }
  }
</script>

<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



