<template>
  <el-form :label-position="labelPosition" :rules="rules" label-width="100px"  ref="formLabelAlign" :model="formLabelAlign"  >
    <el-form-item
      prop="psw"
      label=":"
    >
      <el-input v-model="formLabelAlign.psw"></el-input>
    </el-form-item>
    <el-form-item
      label=":"
      prop="okpsw"
    >
      <el-input v-model="formLabelAlign.okpsw"></el-input>
    </el-form-item>
    <el-form-item
      label=":"
      prop="phone"
    >
      <el-input v-model="formLabelAlign.phone"></el-input>
    </el-form-item>
    <el-form-item
      label=":"
      prop="email">
      <el-input v-model="formLabelAlign.email"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary"></el-button>
      <el-button @click="resetForm('formLabelAlign')" ></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  export default {
    data() {
      return {
        labelPosition: 'right',
        formLabelAlign: {
          psw: '',
          okpsw: '',
          email: '',
          phone:''
        },
        rules: {
            psw:[
              { required: true, message: '', trigger: 'blur' },
              { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
            ],
            okpsw:[
            { required: true, message: '', trigger: 'blur' },
          ],
            phone:[
             { min: 11, max: 11, message: '', trigger: 'blur' }

            ],
            email:[
              { type: 'email', message: '', trigger: 'blur,change' }
            ]
        }
      }
    },
    methods: {
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style>

</style>



